@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,700&display=swap);
html * {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  margin: 0;
}

.case-wrapper{margin:0 50px}.case-wrapper .case{position:relative;display:flex;flex-direction:row;justify-content:flex-start;flex-wrap:wrap}.case-wrapper .case .svg-wrapper{flex:1 1 220px}

header{border-bottom:5px solid black;margin:1em 0 0;padding:0 1em 1em}header p{margin:0;margin-left:2px;font-weight:200;width:30em;max-width:100%;line-height:1.3em}header p:not(:first-of-type){font-style:italic}

.svg-wrapper{width:100%;flex:1 1;display:flex;flex-direction:row;justify-content:center;align-content:center;align-items:center;margin-top:40px}.svg-main{width:200px;height:440px;box-shadow:-10px 5px 12px #aaa;border-radius:2px}.svg-main #logo text{font-family:"Playfair Display", serif;font-size:12px;fill:#333}.svg-main.steelframe{background-color:#c434a3}.svg-main.steelframe #logo ellipse{fill:#c434a3}.svg-main.storage{background-color:#0090cc}.svg-main.storage #logo ellipse{fill:#0090cc}.svg-main.multi-seating{background-color:#ffeb10}.svg-main.multi-seating #logo ellipse{fill:#ffeb10}.svg-main.mod-seating{background-color:#23ad4d}.svg-main.mod-seating #logo ellipse{fill:#23ad4d}.svg-main.office-groups{background-color:#ff4b01}.svg-main.office-groups #logo ellipse{fill:#ff4b01}

